/*****************************/
/**** Responsive Styling ****/
/*****************************/
@media screen and (max-width: 800px) {
    .navigation-toggle {
        display: block;
    }
    .navigation-menu {
        display: none;
    }
    .navigation-menu-mobile.open {
        opacity: 1;
        max-height: 120vh;
    }
    .searchContainer {
        width: 180px;
        margin: 0.6em;
    }
    .\--with-shadow .navigation-menu {
        display: flex;
    }
    .\--with-shadow .navigation-toggle {
        display: none;
    }
    .\--with-shadow .navigation-menu-item.no-mobile {
        display: none;
    }
    .navigation.no-mobile {
        display: none !important;
    }
    .navigation.mobile {
        display: block;
    }
    .searchContainer.no-mobile {
        display: none;
    }
    .navigation-menu-item.only-mobile {
        display: block;
    }
}
