}

.gclout-modal-close-btn {
  position: fixed;
  top: 0;
  right: 7em;
  background: #ffffff;
  width: 2rem;
  height: 2rem;
  padding: 0;
  border: 0;
  cursor: pointer;
  outline: 0;
  box-shadow: 0, 0, 0.625rem, rgba(0, 0, 0, 0.2);
}
.gclout-modal-close-btn:before,
.gclout-modal-close-btn:after {
  content: "";
  position: absolute;
  top: 1rem;
  left: 0.25rem;
  width: 1.4rem;
  height: 0.1rem;
  background-color: #888888;
}
.gclout-modal-close-btn:before {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.gclout-modal-close-btn:after {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
.gclout-modal-close-btn:hover:after,
.gclout-modal-close-btn:hover:before {
  background-color: #00b0f0;
}

